import {
  forwardRef,
  ComponentPropsWithoutRef,
  ReactNode,
  type ElementType,
  type JSX,
  type JSXElementConstructor,
} from "react";
import cx from "classnames";

import styles from "./FieldLabel.module.scss";
import { TooltipProps } from "../Tooltip";
import { SrOnly } from "../SrOnly";
import { FieldLabelMoreInfoIcon } from "./internal/FieldLabelMoreInfoIcon";

type IntrinsicAttributes<
  E extends keyof JSX.IntrinsicElements | JSXElementConstructor<unknown>,
> = JSX.LibraryManagedAttributes<E, ComponentPropsWithoutRef<E>>;

type FieldLabelOwnProps<E extends ElementType = ElementType> = {
  /** The HTML element to render */
  el?: E;
  /** Unique identifier for the label element */
  id?: string;
  /**
   * Whether the field is required. Shows a red asterisk (*) when true.
   * @default false
   */
  required?: boolean;
  /** Additional information to display in a tooltip */
  moreInfo?: ReactNode;
  /**
   * Controls the open state of the more info tooltip.
   * @default false
   */
  openMoreInfo?: TooltipProps["open"];
};

/**
 * Props for the FieldLabel component.
 */
export type FieldLabelProps<E extends ElementType = ElementType> =
  FieldLabelOwnProps<E> &
    Omit<IntrinsicAttributes<E>, keyof FieldLabelOwnProps>;

/**
 * FieldLabel component for form field labels with optional required indicator and help tooltip.
 *
 * Features:
 * - Accessible form label with proper semantic HTML
 * - Optional required field indicator with red asterisk
 * - Help tooltip with info icon for additional context
 * - Screen reader support for required field announcement
 * - Controlled and uncontrolled tooltip state management
 * - Flexible content support for label text and help information
 * - Proper ARIA attributes and semantic structure
 * - Responsive design with consistent typography
 * - Polymorphic element support for different HTML elements
 *
 * @example
 * <FieldLabel as="label" htmlFor="email" required>
 *   Email Address
 * </FieldLabel>
 * <input id="email" type="email" />
 *
 * @example
 * <FieldLabel
 *   el="label"
 *   htmlFor="password"
 *   required
 *   moreInfo="Password must be at least 8 characters long"
 * >
 *   Password
 * </FieldLabel>
 * <input id="password" type="password" />
 *
 * @example
 * <FieldLabel el="legend" required>
 *   Form Section
 * </FieldLabel>
 */
const FieldLabelElement = forwardRef(
  (
    {
      el: Element = "label",
      children,
      className,
      moreInfo,
      required,
      openMoreInfo,
      ...props
    }: FieldLabelProps,
    ref,
  ) => {
    const FieldLabelClassNames = cx([styles["label"]], className);

    return (
      <Element
        className={FieldLabelClassNames}
        data-anv="label"
        ref={ref}
        {...props}
      >
        <div className={styles["top-container"]} aria-atomic="true">
          <span className={styles["label-text"]}>
            {children}
            {required ? (
              <>
                <span aria-hidden className={styles["required"]}>
                  *
                </span>
                <SrOnly>Required</SrOnly>
              </>
            ) : null}
          </span>
          {moreInfo ? (
            <FieldLabelMoreInfoIcon
              moreInfo={moreInfo}
              openMoreInfo={openMoreInfo}
            />
          ) : null}
        </div>

        {moreInfo ? <SrOnly>{moreInfo}</SrOnly> : null}
      </Element>
    );
  },
);

FieldLabelElement.displayName = "FieldLabel";

export const FieldLabel = FieldLabelElement as <
  E extends ElementType = "label",
>(
  props: FieldLabelProps<E>,
) => JSX.Element;
