import { FieldLabelProps } from "../FieldLabel";
import { Tooltip } from "../../Tooltip";
import { Icon } from "../../Icon";
import Info from "@servicetitan/hammer-icon/mdi/round/info.svg";
import styles from "../FieldLabel.module.scss";
import { useState } from "react";

/**
 * Internal component for rendering the info icon with tooltip.
 *
 * @param props - Component props
 * @param props.moreInfo - The content to display in the tooltip
 * @param props.openMoreInfo - Controls the tooltip open state
 * @param props.listenForHover - Whether to listen for hover events
 */
export const FieldLabelMoreInfoIcon = ({
  moreInfo,
  openMoreInfo,
  listenForHover = false,
}: {
  moreInfo: FieldLabelProps["moreInfo"];
  openMoreInfo: FieldLabelProps["openMoreInfo"];
  listenForHover?: boolean;
}) => {
  const [hovered, setHovered] = useState(false);
  const open = listenForHover && hovered ? true : openMoreInfo;
  return (
    <Tooltip
      open={open}
      placement="top"
      offset={5}
      fallbackPlacements={["right", "bottom", "left"]}
    >
      <Tooltip.Trigger
        className={styles["info-trigger"]}
        onMouseEnter={() => setHovered(true)}
        onMouseLeave={() => setHovered(false)}
      >
        <Icon svg={Info} />
      </Tooltip.Trigger>
      <Tooltip.Content className={styles["info-content"]}>
        {moreInfo}
      </Tooltip.Content>
    </Tooltip>
  );
};
